<template>
  <el-dialog ref='term-table' title="标注术语列表" :visible.sync='dialogTableVisible' size="large">
    <div id="mark-result">
      <el-table
        element-loading-text="加载数据"
        v-loading="loading"
        :data="AnnotationDatas"
        style="width:100%"  
        empty-text="暂无数据"
        height="600"
        highlight-current-row>
        <el-table-column  min-width="100px" label="术语ID" header-align="center">
          <template slot-scope="brat">
              <span>{{brat.row.id}}</span>
          </template>
        </el-table-column>
        <el-table-column min-width="300px" label="最终标注" header-align="center">
          <template slot-scope="brat">
              <malgo-brat v-bind:brat-data='brat.row.bratData' 
                :brat-config-data='bratConfig' 
                :brat-id='brat.row.id'>
              </malgo-brat>
            </template>
        </el-table-column>
        <el-table-column min-width="120px" label="更新时间" header-align="center">
          <template slot-scope="brat">
              <span>{{brat.row.gmtModified}}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>   
  </el-dialog>
</template>
<script>
    import MalgoBrat from '@/components/MalgoBrat.vue'
    export default {
      props: ['AnnotationDatas','bratConfig'],
      data () {
        return {
          loading:true,
          dialogTableVisible: false
        };
      },
      mounted () {
       // this.loadJudge();
      },
      updated(){
        this.loadJudge();
      },
      methods: {
        loadJudge(){
          var self=this
          if(self.AnnotationDatas!=null&&self.AnnotationDatas.length>0)
          {
            self.loading=false
          }
        },
        open () {
          var self=this
          self.dialogTableVisible = true
        },
        mySubmit: function(){
          var self=this
          self.dialogTableVisible = false
          self.submit()
        }
      },
      components:{
        'malgo-brat':MalgoBrat
      }
    }
</script>


